<template>
  <v-container>
    <v-layout text-xs-center wrap>
      <v-flex xs12>
        <v-data-table :headers="headers" :items="items" hide-actions class="elevation-1">
          <template slot="items" slot-scope="props">
            <td class="text-xs-left" v-for="(header, i) in headers.filter(h => h.value)" v-text="props.item[header.value]" :key="`car-${props.item.carId}-${i}`"/>
            <td class="justify-center layout px-0">
              <v-icon color="primary" @click="edit(props.item)" class="mx-2">mdi-pencil</v-icon>
              <v-icon color="primary" @click="remove(props.item)" class="mx-2">mdi-delete</v-icon>
            </td>
          </template>
        </v-data-table>
      </v-flex>

      <v-btn @click="edit({})" color="accent" fixed dark fab bottom right>
        <v-icon>mdi-plus</v-icon>
      </v-btn>
      <UserDialog :dialog="dialog" :value="items[editedIndex] || {}" @input="save" @cancel="cancel"/>
    </v-layout>
  </v-container>
</template>

<script>
import faker from 'faker'

import UserDialog from '@/components/UserDialog'

export default {
  components: { UserDialog },
  data: () => ({
    editedIndex: -1,
    dialog: false,
    headers: [
      { text: 'First Name', value: 'firstName' },
      { text: 'Last Name', value: 'lastName' },
      { text: 'Email', value: 'email' },
      { text: 'Role', value: 'role' },
      { text: '', value: null, sortable: false }
    ],
    items: [...Array(8)].map(() => ({
      userId: faker.random.number(),
      firstName: faker.name.firstName(),
      lastName: faker.name.lastName(),
      email: faker.internet.email(),
      role: Math.random() < 0.9 ? 'user' : 'admin'
    }))
  }),
  methods: {
    edit (item) {
      this.editedIndex = this.items.indexOf(item)
      this.dialog = true
    },
    save (item) {
      if (this.editedIndex > -1) {
        Object.assign(this.items[this.editedIndex], item)
      } else {
        this.items.push(item)
      }
      this.dialog = false
    },
    remove (item) {
      if (
        confirm('Are you sure you want to delete this item?')
      ) {
        this.items.splice(this.items.indexOf(item), 1)
      }
    },
    cancel (item) {
      this.dialog = false
    }
  }
}
</script>

