<template>
  <v-dialog :value="dialog" persistent max-width="600px">
    <v-card>
      <v-card-title>
        <span class="headline">User Profile</span>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex xs12 md6>
              <v-text-field label="First name*" v-model="item.firstName" required/>
            </v-flex>
            <v-flex xs12 md6>
              <v-text-field label="Last name*" v-model="item.lastName" required/>
            </v-flex>
            <v-flex xs12>
              <v-text-field label="Email*" v-model="item.email" required/>
            </v-flex>
            <v-flex xs12>
              <v-text-field label="Password*" v-model="item.password" type="password" required/>
            </v-flex>
            <v-flex xs12>
              <v-select label="Role*" :items="roles" v-model="item.role" required/>
            </v-flex>
          </v-layout>
        </v-container>
        <small>*indicates required field</small>
      </v-card-text>
      <v-card-actions>
        <v-spacer/>
        <v-btn color="primary" flat @click.native="cancel()">Cancel</v-btn>
        <v-btn color="primary" flat @click.native="save()">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    value: Object,
    dialog: Boolean,
    button: {
      type: Boolean,
      default: true
    }
  },
  data: () => ({
    item: {},
    roles: [{
      text: 'User',
      value: 'user'
    }, {
      text: 'Admin',
      value: 'admin'
    }]
  }),
  watch: {
    dialog (val) {
      if (val) {
        this.item = JSON.parse(JSON.stringify(this.value))
      }
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel')
    },
    save () {
      // @todo: save to the server
      this.$emit('input', this.item)
    }
  }
}
</script>
