<template>
  <v-dialog :value="dialog" persistent max-width="600px">
    <v-card>
      <v-card-title>
        <span class="headline">Car</span>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex xs12>
              <v-text-field label="Name*" v-model="item.name" required/>
            </v-flex>
            <v-flex xs12>
              <v-text-field label="Owner*" v-model="item.owner" required/>
            </v-flex>
            <v-flex xs12>
              <v-switch label="Certification*" v-model="item.certification" required/>
            </v-flex>
            <v-flex xs12>
              <DateTime label="Last revision" v-model="item.lastRevision"/>
            </v-flex>
            <v-flex xs12>
              <DateTime label="Last ZTE" v-model="item.lastZTE"/>
            </v-flex>
            <v-flex xs12>
              <DateTime label="Last ZTL" v-model="item.lastZTL"/>
            </v-flex>
          </v-layout>
        </v-container>
        <small>*indicates required field</small>
      </v-card-text>
      <v-card-actions>
        <v-spacer/>
        <v-btn color="primary" flat @click.native="cancel()">Cancel</v-btn>
        <v-btn color="primary" flat @click.native="save()">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import DateTime from '@/components/DateTime'

export default {
  components: { DateTime },
  props: {
    value: Object,
    dialog: Boolean,
    button: {
      type: Boolean,
      default: true
    }
  },
  data: () => ({
    item: {}
  }),
  watch: {
    dialog (val) {
      if (val) {
        this.item = JSON.parse(JSON.stringify(this.value))
      }
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel')
    },
    save () {
      // @todo: save to the server
      this.$emit('input', this.item)
    }
  }
}
</script>
